.vkuiPanel.vkuiPanel--regular .vkuiPanel__in {
    background: var(--background_content);
}

.Placeholder {
    margin: 74px 195px 64px 96px
}

.Main {
    margin-top: 48px;
    margin-left: 96px;
    width: 319px;
}

.Main .Colors__Container {
    padding: 0 24px;
    margin: 16px -24px 0;
}

.PlayContainer {
    position: absolute;
    top: 48px;
    right: 126px;
}

.Cards__Container > :last-child, .Cards2__Container > :last-child, .StatsCards__Container > :last-child {
    margin-left: 23px;
}

.vkuiPanelHeader__fixed {
    position: relative;
    margin: 48px 96px 0 80px;
}

.vkuiPanelHeader--android::before {
    height: 0;
}

.Gallery, .Rating, .Likes, .Author, .Tags, .Friends, .FriendLobby, .Stats, .Shop, .GlobalCanvasInfo {
    margin: 18px 96px 0 96px;
}

.Gallery p, .Rating p, .Likes p, .GlobalCanvasInfo p {
    margin: 0;
}

.Search {
    margin: 89px 190px;
}

.Search h1 {
    margin: 0;
}

.Game {
    margin: 38px 18px 0 48px;
}

.Comments_Container {
    height: 319px;
    width: 240px;
    margin-left: 35px;
}

.Comments {
    max-height: 263px;
    justify-content: end;
}

.Comment_Input {
    margin-top: 14px;
    width: 240px;
}

.Result {
    margin: 53px 171px 53px 170px;
}

.Result .Button--lvl-secondary {
    margin-top: 28px;
}

.Canvas_Container, .Canvas_Container_Clear {
    position: relative;
    width: 319px;
    height: 319px;
    margin-top: 28px;

    background: #FFFFFF;
    box-shadow: 0px 0px 227px rgba(0, 0, 0, 0.15);
    border-radius: 28px;
    overflow: hidden;
}

.Likes__Container {
    width: 310px;
}

.PictureInfo {
    margin: 18px 48.5px 0 96px;
}

.PictureInfo > .Canvas_Container .Actions_Container .LikeButton {
    width: 148px;
}

.PictureInfo > .Author_Container {
    width: 319px;
}

.PictureInfo > .Actions_Container {
    position: absolute;
    top: 122px;
    right: 48.5px;
    width: 154.5px;
}

.Offline_Drawing__Buttons {
    width: 319px;
}

.vkuiPromoBanner {
    width: 333px;
    bottom: 18px;
}

.MainTint {
    display: none;
}

.CardButton {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--border_color);
    border-radius: 21px;
    cursor: pointer;
    color: var(--color_secondary2);
    position: absolute;
    top: 316px;
    left: calc(263px + (144px - 12px - 12px) + 23px + 23px);
    width: 144px;
    height: 165px;
}

.CardButton span {
    margin-top: 4px;
}

.app_version {
    display: none;
}

.Main .PlayCards__Container {
    position: absolute;
    left: 458px;
    top: 47px;
    width: 154px;
}

.Main .PlayCards_Lobbies__Container > :last-child {
    margin-top: 8px;
}

.Main .PlayerPics_Container {
    margin-top: 4px;
}

.Main .SubCards__Container > * {
    color: var(--color_secondary);
    background: var(--border_color);
}

.Game .Canvas_Items_Container {
    width: 319px;
}

.Game .vkuiSlider, .GlobalCanvas .vkuiSlider {
    width: 319px;
}

.Game .Offline_Drawing__Buttons {
    position: absolute;
    right: 0;
    display: block;
}

.Game .Offline_Drawing__Buttons {
    position: absolute;
    top: 92px;
    right: 56px;
    display: block;
    width: 188px;
}

.Game .Offline_Drawing__Buttons > * {

}

.CircleButtonBack {
    position: fixed;
    width: 48px;
    height: 48px;
    left: 56px;
    top: 56px;

    background: var(--background_content);
    box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.12);
    border-radius: 48px;

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;

    z-index: 2;
}